// @flow
import React, { Component } from "react";
import { styled } from "fusion-plugin-styletron-react";

import Album from "./album.js";

class Home extends Component {
  constructor() {
    super();
    this.state = {
      albumsArray: [
        [
          {
            height: 100,
            width: 300
          },
          {
            height: 200,
            width: 300
          },
          {
            height: 250,
            width: 200
          }
        ],
        [
          {
            height: 420,
            width: 300
          },
          {
            height: 200,
            width: 300
          },
          {
            height: 500,
            width: 300
          }
        ],
        [
          {
            height: 360,
            width: 300
          },
          {
            height: 200,
            width: 300
          }
        ]
      ]
    };
  }
  render() {
    console.log("this.state.albumsArray", this.state.albumsArray);
    return (
      <HomeContainer>
        <h1>Image Gallery</h1>
        <button>+</button>
        <div>
          {this.state.albumsArray.map((album, i) => (
            <div key={i}>
              {album.map((x, i) => (
                <div
                  style={{ height: x.height, width: x.width, ...bg }}
                  key={i}
                >
                  {album.height}x
                </div>
              ))}
            </div>
          ))}
        </div>
      </HomeContainer>
    );
  }
}

const HomeContainer = styled("div", {
  fontFamily: "HelveticaNeue-Light, Arial",
  margin: "auto",
  width: "80%"
});

const bg = {
  backgroundColor: "gray"
};

export default Home;
